.actors-link-title, .ganres__link-title, .trending__link-title {
  padding: 10px, 10px;
  margin-bottom: 40px;
  font-size: 28px;
  font-weight: 700;
  line-height: 20px;
  display: block;
  position: relative;
  left: 1.2%;
}

@media screen and (max-width: 360px) {
  .actors-link-title, .ganres__link-title, .trending__link-title {
    font-size: 25px;
  }
}

.actors-link-title > span, .ganres__link-title > span, .trending__link-title > span {
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
}

.actors-link-title:hover > span, .ganres__link-title:hover > span, .trending__link-title:hover > span {
  color: #26fff2;
}

.actors-link-title:hover:after, .ganres__link-title:hover:after, .trending__link-title:hover:after {
  opacity: 1;
  transform: scaleX(1);
}

.actors-link-title:before, .ganres__link-title:before, .trending__link-title:before {
  content: " ";
  width: 8px;
  height: 35px;
  background: linear-gradient(317deg, #26fff2 13.2%, #326cff 71.54%);
  border-radius: 50px;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 50%;
  left: -10px;
  transform: translate(-50%, -50%);
}

.actors-link-title:after, .ganres__link-title:after, .trending__link-title:after {
  content: " ";
  width: 100%;
  height: 3px;
  opacity: 0;
  background: linear-gradient(317deg, #26fff2 13.2%, #326cff 71.54%);
  border-radius: 50px;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  bottom: -10px;
  left: 0;
  transform: scaleX(0);
}

.motivation__link--mobile, .motivation__link, .see-more, .watch-more-link {
  filter: drop-shadow(0 4px 50px rgba(50, 108, 255, .5));
  background: linear-gradient(317deg, #26fff2 13.2%, #326cff 71.54%);
  border-radius: 25px;
  justify-content: center;
  align-items: center;
  font-family: Mulish;
  font-size: 16px;
  font-weight: 800;
  line-height: 20px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1), color .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.motivation__link--mobile:hover, .motivation__link:hover, .see-more:hover, .watch-more-link:hover {
  color: #323232;
  transform: scale(1.1);
}

